.put {
    background: linear-gradient(104deg, #C165DD 0%, #5C27FE 100%);
    color: white;
    padding: 6px 20px;
    font-weight: bold;
    font-size: 15px;
    border-radius: 13px;
    border: none;
    cursor: pointer;
}

.editInput {
    width: 75%;
    padding: 10px 10px;
    margin-top: 5%;
    border: 1px solid #1153FC;
    border-end-start-radius: 5px;
    border-start-start-radius: 5px;
    outline: none;
    transition: .65s;
}



.editBut {
    background: linear-gradient(104deg, #5581F1 0%, #1153FC 100%);
    color: white;
    font-weight: bold;
    padding: 10px 20px;
    cursor: pointer;
    border-end-end-radius: 5px;
    border-start-end-radius: 5px;
    border: 1px solid #1153FC;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: .65s;
}