* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

.container {
  width: 1140px;
  margin: 0 auto;
}

.divs {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.divs div {
  display: flex;
  margin-top: 15px;
  justify-content: space-between;
  /* width: ; */
  align-items: center;
}

.div {
  display: inline-block;
  margin-left: 26%;
  margin-top: 50px;
  margin-bottom: 50px;
}

.div button {
  background: rgb(0, 247, 167);
  background: linear-gradient(104deg, #00f7a7 0%, #04f5ed 100%);
  padding: 10px 30px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  border: 1px solid transparent;
  border-radius: 13px;
  cursor: pointer;
  transition: .65s;
}

.div button:hover {
  background: white;
  color:  #04f5ed;
  border: 1px solid #00f7a7;
}
.subtext {
  background: linear-gradient(104deg, #5581F1 0%, #1153FC 100%);
  color: white;
  padding: 10px 15px;
  border-radius: 13px;
  text-align: center;
}

.sendInput {
  width: 90%;
  padding: 10px 10px;
  margin-top: 5%;
  border: 1px solid #1153FC;
  border-end-start-radius: 5px;
  border-start-start-radius: 5px;
  outline: none;
  transition: .65s;
}
.sendInput:focus{
  box-shadow: 0px 0px 20px 0px #04f5ed;
}
.sendBut {
  background: linear-gradient(104deg, #5581F1 0%, #1153FC 100%);
  color: white;
  font-weight: bold;
  padding: 10px 20px;
  cursor: pointer;
  border-end-end-radius: 5px;
  border-start-end-radius: 5px; 
  border: 1px solid #1153FC;
  text-transform: uppercase; 
  letter-spacing: 1px;
  transition: .65s;
}
.sendBut:hover {
  box-shadow: 0px 0px 20px 0px #04f5ed;
  color: #1153FC;
  background: white;
}
.name {
  text-align: center;
  font-size: 25px;
  letter-spacing: 1px;
  margin: 2% 0;
}